import React, { useState, useEffect } from 'react';

interface LockScreenProps {
  theme: string;
  onUnlock: () => void;
}

const PIN_KEY = 'app_pin';

const LockScreen: React.FC<LockScreenProps> = ({ theme, onUnlock }) => {
  const [pin, setPin] = useState('');
  const [step, setStep] = useState<'enter' | 'set' | 'confirm' | 'change' | 'old' | 'new' | 'confirmNew'>('enter');
  const [input, setInput] = useState('');
  const [newPin, setNewPin] = useState('');
  const [error, setError] = useState('');
  const [showPin, setShowPin] = useState(false);

  useEffect(() => {
    const savedPin = localStorage.getItem(PIN_KEY);
    if (savedPin) {
      setPin(savedPin);
      setStep('enter');
    } else {
      setStep('set');
    }
  }, []);

  const handleInput = (e: React.ChangeEvent<HTMLInputElement>) => {
    setInput(e.target.value.replace(/\D/g, ''));
    setError('');
  };

  const handleShowPin = () => setShowPin((v) => !v);

  const handleEnter = () => {
    if (input === pin) {
      setError('');
      setInput('');
      onUnlock();
    } else {
      setError(' PIN');
    }
  };

  const handleSet = () => {
    if (input.length < 4) {
      setError(' 4 ');
      return;
    }
    setNewPin(input);
    setInput('');
    setStep('confirm');
  };

  const handleConfirm = () => {
    if (input === newPin) {
      localStorage.setItem(PIN_KEY, newPin);
      setPin(newPin);
      setInput('');
      setNewPin('');
      setStep('enter');
      setError('PIN !');
    } else {
      setError('PIN  ');
    }
  };

  const handleChange = () => {
    setStep('old');
    setInput('');
    setError('');
  };

  const handleOld = () => {
    if (input === pin) {
      setStep('new');
      setInput('');
      setError('');
    } else {
      setError('  PIN');
    }
  };

  const handleNew = () => {
    if (input.length < 4) {
      setError(' 4 ');
      return;
    }
    setNewPin(input);
    setInput('');
    setStep('confirmNew');
  };

  const handleConfirmNew = () => {
    if (input === newPin) {
      localStorage.setItem(PIN_KEY, newPin);
      setPin(newPin);
      setInput('');
      setNewPin('');
      setStep('enter');
      setError('PIN !');
    } else {
      setError('PIN  ');
    }
  };

  const handleReset = () => {
    localStorage.removeItem(PIN_KEY);
    setPin('');
    setStep('set');
    setError('PIN !');
  };

  //   
  const bg = theme === 'dark' ? 'bg-gray-900 text-orange-200' : 'bg-orange-50 text-gray-800';
  const inputStyle = theme === 'dark' ? 'bg-gray-800 text-orange-200 border-orange-400' : 'bg-white text-gray-800 border-orange-400';
  const btnStyle = theme === 'dark' ? 'bg-orange-600 text-white' : 'bg-orange-500 text-white';

  return (
    <div className={`fixed inset-0 z-50 flex flex-col items-center justify-center ${bg}`}
         style={{ minHeight: '100vh' }}>
      <div className="w-full max-w-xs p-6 rounded-xl shadow-xl border border-orange-300 flex flex-col gap-4 items-center">
        <div className="text-2xl font-bold mb-2"></div>
        {step === 'enter' && (
          <>
            <div> PIN  </div>
            <input
              type={showPin ? 'text' : 'password'}
              className={`w-full p-2 rounded border ${inputStyle}`}
              value={input}
              onChange={handleInput}
              maxLength={8}
              autoFocus
            />
            <button onClick={handleShowPin} className="text-xs underline">{showPin ? '' : ''} PIN</button>
            <button className={`w-full p-2 rounded ${btnStyle} mt-2`} onClick={handleEnter}></button>
            <button className="text-xs underline mt-1" onClick={handleChange}> PIN</button>
            <button className="text-xs underline mt-1" onClick={handleReset}> </button>
          </>
        )}
        {step === 'set' && (
          <>
            <div>  PIN ( 4 )</div>
            <input
              type={showPin ? 'text' : 'password'}
              className={`w-full p-2 rounded border ${inputStyle}`}
              value={input}
              onChange={handleInput}
              maxLength={8}
              autoFocus
            />
            <button onClick={handleShowPin} className="text-xs underline">{showPin ? '' : ''} PIN</button>
            <button className={`w-full p-2 rounded ${btnStyle} mt-2`} onClick={handleSet}></button>
          </>
        )}
        {step === 'confirm' && (
          <>
            <div> PIN</div>
            <input
              type={showPin ? 'text' : 'password'}
              className={`w-full p-2 rounded border ${inputStyle}`}
              value={input}
              onChange={handleInput}
              maxLength={8}
              autoFocus
            />
            <button onClick={handleShowPin} className="text-xs underline">{showPin ? '' : ''} PIN</button>
            <button className={`w-full p-2 rounded ${btnStyle} mt-2`} onClick={handleConfirm}></button>
          </>
        )}
        {step === 'old' && (
          <>
            <div>  PIN</div>
            <input
              type={showPin ? 'text' : 'password'}
              className={`w-full p-2 rounded border ${inputStyle}`}
              value={input}
              onChange={handleInput}
              maxLength={8}
              autoFocus
            />
            <button onClick={handleShowPin} className="text-xs underline">{showPin ? '' : ''} PIN</button>
            <button className={`w-full p-2 rounded ${btnStyle} mt-2`} onClick={handleOld}></button>
            <button className="text-xs underline mt-1" onClick={() => setStep('enter')}></button>
          </>
        )}
        {step === 'new' && (
          <>
            <div>  PIN ( 4 )</div>
            <input
              type={showPin ? 'text' : 'password'}
              className={`w-full p-2 rounded border ${inputStyle}`}
              value={input}
              onChange={handleInput}
              maxLength={8}
              autoFocus
            />
            <button onClick={handleShowPin} className="text-xs underline">{showPin ? '' : ''} PIN</button>
            <button className={`w-full p-2 rounded ${btnStyle} mt-2`} onClick={handleNew}></button>
          </>
        )}
        {step === 'confirmNew' && (
          <>
            <div>  PIN</div>
            <input
              type={showPin ? 'text' : 'password'}
              className={`w-full p-2 rounded border ${inputStyle}`}
              value={input}
              onChange={handleInput}
              maxLength={8}
              autoFocus
            />
            <button onClick={handleShowPin} className="text-xs underline">{showPin ? '' : ''} PIN</button>
            <button className={`w-full p-2 rounded ${btnStyle} mt-2`} onClick={handleConfirmNew}></button>
          </>
        )}
        {error && <div className="text-red-500 text-sm mt-2">{error}</div>}
      </div>
    </div>
  );
};

export default LockScreen; 